<template>
<HeaderComponent1 />

  <div>
    <h1>부모 컴포넌트</h1>
    <p>자식으로부터 받은 메시지: {{ messageFromChild }}</p>
    <input v-model="parentMessage" placeholder="자식에게 보낼 메시지" />
    <button @click="openChild">자식 컴포넌트 열기</button>

    <Test2Child
      v-if="isChildOpen"
      :parentMessage="parentMessage"
      @message-from-child="receiveMessage"
      @close="isChildOpen = false"
    />
  </div>
</template>

<script>
import HeaderComponent1 from '../components/HeaderComponent1.vue';
import Test2Child from '../components/Test2Child.vue';

export default {
  name: 'childParentTest',
  components: {
    HeaderComponent1,
    Test2Child
  },
  data() {
    return {
      parentMessage: '',
      messageFromChild: '',
      isChildOpen: false
    };
  },
  methods: {
    openChild() {
      this.isChildOpen = true;
    },
    receiveMessage(message) {
      this.messageFromChild = message;
      this.isChildOpen = false;
    }
  }
};
</script>

<style scoped>
