<template>
<HeaderComponent1 />

  <div class="fish-shop">
    <img src="../assets/fish-shop-logo.png" alt="Fish Shop Logo" class="logo">
    <h1>생선가게에 오신 것을 환영합니다</h1>
    <ul class="fish-list">
      <li v-for="fish in displayedFishList" :key="fish.id" class="fish-item">
        <img :src="fish.image" :alt="fish.name" class="fish-image">
        <div class="fish-info">
          <h2>{{ fish.name }}</h2>
          <p>{{ fish.description }}</p>
          <p><strong>가격:</strong> {{ fish.price }}원</p>
        </div>
      </li>
    </ul>
    <div v-if="!isEnd" ref="loadMore" class="load-more">Loading...</div>

    <ScrollTopButton />
  </div>
</template>

<script>
import HeaderComponent1 from '../components/HeaderComponent1.vue';
import ScrollTopButton from '../components/ScrollTopButton.vue';
import { fishList } from '../data/fishData.js';

export default {
  name: 'FishShopTest',
  data() {
    return {
      fishList,
      displayedFishList: [],
      itemsPerPage: 3,
      currentPage: 1,
      isEnd: false,
      observer: null
    };
  },
  components: {
    HeaderComponent1,
    ScrollTopButton
  },
  mounted() {
    this.loadMoreItems();
    this.initObserver();
  },
  methods: {
    loadMoreItems() {
      const startIndex = (this.currentPage - 1) * this.itemsPerPage;
      const endIndex = this.currentPage * this.itemsPerPage;
      if (startIndex < this.fishList.length) {
        this.displayedFishList = this.displayedFishList.concat(this.fishList.slice(startIndex, endIndex));
        this.currentPage++;
      } else {
        this.isEnd = true;
        if (this.observer) {
          this.observer.disconnect();
        }
      }
    },
    initObserver() {
      const options = {
        root: null,
        rootMargin: '0px',
        threshold: 1.0
      };
      this.observer = new IntersectionObserver(this.handleIntersect, options);
      if (this.$refs.loadMore) {
        this.observer.observe(this.$refs.loadMore);
      }
    },
    handleIntersect(entries) {
      if (entries[0].isIntersecting) {
        this.loadMoreItems();
      }
    }
  },
  beforeUnmount() {
    if (this.observer) {
      this.observer.disconnect();
    }
  }
};
</script>

<style scoped>
.fish-shop {
  text-align: center;
  padding: 20px;
}

.logo {
  width: 150px;
  margin-bottom: 20px;
}

.fish-list {
  list-style: none;
  padding: 0;
}

.fish-item {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.fish-image {
  width: 100px;
  height: 100px;
  margin-right: 20px;
}

.fish-info {
  text-align: left;
}

.load-more {
  text-align: center;
  padding: 10px;
  font-weight: bold;
}
</style>
