import { createStore } from 'vuex';

const store = createStore({
  state: {
    name: '',
    birthdate: '',
    age: null,
    gender: '',
    hobby: ''
  },
  mutations: {
    setName(state, name) {
      state.name = name;
    },
    setBirthdate(state, birthdate) {
      state.birthdate = birthdate;
    },
    setAge(state, age) {
      state.age = age;
    },
    setGender(state, gender) {
      state.gender = gender;
    },
    setHobby(state, hobby) {
      state.hobby = hobby;
    }
  },
  actions: {
    updateName({ commit }, name) {
      commit('setName', name);
    },
    updateBirthdate({ commit }, birthdate) {
      commit('setBirthdate', birthdate);
    },
    updateAge({ commit }, age) {
      commit('setAge', age);
    },
    updateGender({ commit }, gender) {
      commit('setGender', gender);
    },
    updateHobby({ commit }, hobby) {
      commit('setHobby', hobby);
    }
  },
  getters: {
    getName: state => state.name,
    getBirthdate: state => state.birthdate,
    getAge: state => state.age,
    getGender: state => state.gender,
    getHobby: state => state.hobby
  }
});

export default store;