<template>
  <div class="modal">
    <div class="modal-content">
      <span class="close-button" @click="close">&times;</span>
      <h2>자식 컴포넌트</h2>
      <p>부모로부터 받은 메시지: {{ parentMessage }}</p>
      <input v-model="childMessage" placeholder="부모에게 보낼 메시지" />
      <button @click="sendMessage">메시지 보내기</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Test2Child',
  props: ['parentMessage'],
  data() {
    return {
      childMessage: ''
    };
  },
  methods: {
    sendMessage() {
      this.$emit('message-from-child', this.childMessage);
    },
    close() {
      this.$emit('close');
    }
  }
};
</script>

<style scoped>
.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
  background-color: #fefefe;
  padding: 20px;
  border: 1px solid #888;
  width: 300px;
  text-align: center;
  position: relative;
}

.close-button {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close-button:hover,
.close-button:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}
</style>