<template>
<HeaderComponent1 />

  <div class="profile-form">
    <h1>사용자 정보 입력</h1>
    <form @submit.prevent="submitForm">
      <div>
        <label for="name">이름:</label>
        <input type="text" id="name" v-model="name" />
      </div>
      <div>
        <label for="birthdate">생년월일:</label>
        <input type="date" id="birthdate" v-model="birthdate" />
      </div>
      <div>
        <label for="age">나이:</label>
        <input type="number" id="age" v-model="age" />
      </div>
      <div>
        <label for="gender">성별:</label>
        <select id="gender" v-model="gender">
          <option value="남성">남성</option>
          <option value="여성">여성</option>
          <option value="기타">기타</option>
        </select>
      </div>
      <div>
        <label for="hobby">취미:</label>
        <input type="text" id="hobby" v-model="hobby" />
      </div>
      <button type="submit">저장</button>
    </form>
    <div class="profile-info">
      <h2>저장된 사용자 정보</h2>
      <p><strong>이름:</strong> {{ name }}</p>
      <p><strong>생년월일:</strong> {{ birthdate }}</p>
      <p><strong>나이:</strong> {{ age }}</p>
      <p><strong>성별:</strong> {{ gender }}</p>
      <p><strong>취미:</strong> {{ hobby }}</p>
    </div>
  </div>
</template>

<script>
import HeaderComponent1 from '../components/HeaderComponent1.vue';
import { mapGetters, mapActions } from 'vuex';

export default {
  name: 'ProfileForm',
  components: {
    HeaderComponent1
  },
  computed: {
    ...mapGetters(['getName', 'getBirthdate', 'getAge', 'getGender', 'getHobby']),
    name: {
      get() {
        return this.getName;
      },
      set(value) {
        this.updateName(value);
      }
    },
    birthdate: {
      get() {
        return this.getBirthdate;
      },
      set(value) {
        this.updateBirthdate(value);
      }
    },
    age: {
      get() {
        return this.getAge;
      },
      set(value) {
        this.updateAge(value);
      }
    },
    gender: {
      get() {
        return this.getGender;
      },
      set(value) {
        this.updateGender(value);
      }
    },
    hobby: {
      get() {
        return this.getHobby;
      },
      set(value) {
        this.updateHobby(value);
      }
    }
  },
  methods: {
    ...mapActions(['updateName', 'updateBirthdate', 'updateAge', 'updateGender', 'updateHobby']),
    submitForm() {
      // 데이터는 이미 v-model을 통해 업데이트되므로 추가 작업은 필요하지 않습니다.
      alert('저장되었습니다!');
    }
  }
}
</script>

<style scoped>
.profile-form {
  padding: 20px;
}

.profile-form h1 {
  margin-bottom: 20px;
}

.profile-form label {
  display: inline-block;
  width: 100px;
}

.profile-form input,
.profile-form select {
  margin-bottom: 10px;
  padding: 5px;
}

.profile-info {
  margin-top: 20px;
}

.profile-info p {
  margin: 5px 0;
}
</style>